@import url(https://fonts.googleapis.com/css?family=Alef:400,700&subset=hebrew);
.level-item {
  align-items: center;
  display: flex;
  flex-basis: auto;
  flex-grow: 0;
  flex-shrink: 0;
  justify-content: center;
}

.level-item .title,
.level-item .subtitle {
  margin-bottom: 0;
}

@media screen and (max-width: 768px) {
  .level-item:not(:last-child) {
    margin-bottom: 0.75rem;
  }
}

.level-left,
.level-right {
  flex-basis: auto;
  flex-grow: 0;
  flex-shrink: 0;
}

.level-left .level-item:not(:last-child),
.level-right .level-item:not(:last-child) {
  margin-right: 0.75rem;
}

.level-left .level-item.is-flexible,
.level-right .level-item.is-flexible {
  flex-grow: 1;
}

.level-left {
  align-items: center;
  justify-content: flex-start;
}

@media screen and (max-width: 768px) {
  .level-left + .level-right {
    margin-top: 1.5rem;
  }
}

@media screen and (min-width: 769px) {
  .level-left {
    display: flex;
  }
}

.level-right {
  align-items: center;
  justify-content: flex-end;
}

@media screen and (min-width: 769px) {
  .level-right {
    display: flex;
  }
}

.level {
  align-items: center;
  justify-content: space-between;
}

.level:not(:last-child) {
  margin-bottom: 1.5rem;
}

.level code {
  border-radius: 3px;
}

.level img {
  display: inline-block;
  vertical-align: top;
}

.level.is-mobile {
  display: flex;
}

.level.is-mobile > .level-item:not(:last-child) {
  margin-bottom: 0;
}

.level.is-mobile > .level-item:not(.is-narrow) {
  flex-grow: 1;
}

@media screen and (min-width: 769px) {
  .level {
    display: flex;
  }
  .level > .level-item:not(.is-narrow) {
    flex-grow: 1;
  }
}

html {
  font-size: 10px;
  text-align: right;
  direction: rtl;
}

body {
  font-family: Alef, Helvetica, Arial, sans-serif;
  font-size: 13px;
  line-height: 1.3;
}

@media screen and (min-width: 769px) {
  .faces:not(.is-desktop) {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    flex-flow: row wrap;
  }
}

.column {
  display: block;
  -ms-flex-preferred-size: 0;
  flex-basis: 0;
  -webkit-box-flex: 1;
  -ms-flex-positive: 1;
  flex-grow: 1;
  -ms-flex-negative: 1;
  flex-shrink: 1;
  padding: 0.75rem;
}

.faces {
  width: 100%;
  height: 100vh;
}

.member__profile {
  width: 6.66%;
  flex-basis: 6.66%;
  height: 12.5%;
  padding: 0;
  position: relative;
  overflow: hidden;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center 20%;
}

.member__profile h4 {
  position: absolute;
  bottom: 5px;
  color: white;
  font-size: 1.8rem;
  text-shadow: 3px -2px 6px black;
  padding: 0 5px;
}

.member__profile.is-not-present {
  opacity: 0.4;
}

.member__profile__long-name h4 {
  font-size: 1.5rem;
}

.nav {
  border-top: 2px solid #00b1b3;
  z-index: 500;
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
}
